import hljs from 'highlight.js'
import redent from 'redent'

export default {
  props: {
    lang: {
      type: String,
      default: 'html'
    }
  },
  render (h) {
    if ('default' in this.$scopedSlots) {
      // console.log(this.$scopedSlots.default()[0].text)
      // this.$scopedSlots.default()[0].text
      // console.log(this.$slots.default)
      const code = this.$scopedSlots.default()[0].children[0].text
      const html = hljs.highlight(redent(code, 0), {
        language: this.lang
      }).value

      return h('pre', {
        class: 'code-pre'
      }, [
        h('code', {
          class: ['hljs', this.lang, 'bg-transparent'],
          domProps: {
            innerHTML: html
          }
        })
      ])
    }
  }
}
