<template>
  <div :class="classes">
    <a
      href="javascript:void(0)"
      v-if="trigger"
      :class="{'btn-open':show}"
      @click="show=!show"
    >
      {{ i18n.common.showHideCode }}
      <fa-icon icon="caret-down" />
    </a>
    <div
      v-show="display"
      class="code-container"
    >
      <slot />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'Showcode',
  props: {
    expand: { type: Boolean, default: false },
    trigger: { type: Boolean, default: true }
  },
  data () {
    return {
      show: this.expand
    }
  },
  computed: {
    ...mapState(['i18n']),
    display () {
      if (!this.trigger) return true
      return this.show
    },
    classes () {
      return {
        'show-code': true,
        'code-only': !this.trigger
      }
    }
  }
}
</script>

<style lang="scss">
$bg: #F8F9FA;
$deep: #EBEBEB;
div.show-code{
  display: block;
  margin: 5px 0 10px;
  position: relative;
  border-radius: 4px;
  overflow: hidden;
  &.code-only{
    .code-container{
      border-color: $bg;
      background-color: $bg;
    }
  }
  a{
    color: #aaa;
    padding: 5px 10px;
    background-color: white;
    display: block;
    transition: border-color 200ms, color 200ms, background-color 200ms;
    z-index: 100;
    &:hover {
      color: #666;
      background-color: $deep;
      border-color: $bg;
    }
    & > .fa-caret-down { transition:transform .2s ease; }
    &.btn-open{
      background-color: $deep;
      border-color: $bg;
      color: #333;
      &:hover, &:visited, &:focus { color: #333; }
      & > .fa-caret-down { transform: rotate(180deg); }
    }
  }
  .code-container {
    border: 4px solid $deep;
    border-top: 0;
    padding: 0.8rem 1rem;
    background-color: #fdfdfd;
    z-index: 99;
  }
}
</style>
